// import React from 'react';
import '../index.css';
import React, { useEffect, useState } from 'react'
import Chart from "react-apexcharts";



function Card(props){

  const [Persentage, setPersentage]= useState(0)
  const [TimeData, setTimeData]=useState({
      chart: {
          id: "basic-bar"
        },
         xaxis: {
           categories: [0, 0, 0, 0, 0, 0, 0, 0]
         }
      })
  
  const [PriceData, setPriceData]=useState([
    {
      name: "Predictions",
      data:[0, 0, 0, 0, 0, 0, 0, 0]

    },
    {
      name: "Previous ",
      data:[0, 0, 0, 0, 0, 0, 0, 0]
    },
  ])
  
  useEffect(()=>{
    var categories = []
    var historicaldata = []
    var prediction =[]
    
    var api= 'http://127.0.0.1:8000/longterm/?company='+props.comp+'&variable='+props.variable
    if (props.type == 'shortterm') {
      api='http://127.0.0.1:8000/shortterm/?company='+props.comp
    }
         
    fetch(api)
    .then(function(response) {
        return response.json();
    })
    .then(function(data) {
      for(var i=0; i < data.Previous.length ; i++ ){
        categories = categories.concat(Object.keys (data.Previous[i]))
        historicaldata = historicaldata.concat(Object.values (data.Previous[i]))
        prediction[i]=null
      }
      prediction[data.Previous.length-1]=historicaldata[data.Previous.length-1]
      
      let temp=[]
      for (var j = 0 ; j < data.Predictions.length  ; j++ ){
        prediction = prediction.concat( Object.values(data.Predictions[j]))
        temp.push(j+1)
      }   
      categories=categories.concat(temp)

      setTimeData({
        chart: {
          id: "basic-bar"
        },
        xaxis: {
          categories:categories
          }
      })
      setPriceData([
        {
          name: "Predictions",
          data: prediction
        },
        {
          name: "Previous ",
          data: historicaldata
        },
      ])
      let persent = (prediction[prediction.length-1] - historicaldata[historicaldata.length-1] )/historicaldata[historicaldata.length-1]*100
      setPersentage (persent.toFixed(2))
    })
    .catch(function(e){
      console.log(e)  
    })
  
  },[])



  return(
    <div className='card'>
      <div className='infoLayer'>
        <img className='comp-logo' src={require('../Logos/'+props.comp+'.png')} alt="couldn't load" />
        <a href={props.link} target='_blank'> 
          <h2 className='about'>{props.fullname} ({props.comp} )<div className='per-var'>Growth= {Persentage}%                    Variable= {props.variable} </div></h2>
        </a>
      </div>
      <div>
        <Chart
          className='lineChart'
          height='400px'
          options= {TimeData}
          series={PriceData }
          type="line"
          width="100%"
          alignment='center'
        />
      </div>
    </div> 
  ) 
}  
    
export default Card;    