import React from "react";
import Card from "./Components/Card";
import './index.css';

function Home (props){
    const list_companies = ['SYS','FFC','PAEL','LUCK','KAPCO', 'OGDC','HBL','ENGRO','HUBC','PPL', 'POL','EFERT', 'MCB',
                    'UBL','DGKC','PSO','SEARL','MLCF','BAHL','MARI','TRG','ATRL','UNITY',
                    'MEBL','GHNI','PIOC','CHCC','ISL']
    const fullname={ENGRO: 'Engro Corporation Limited',
    LUCK: 'Lucky Cement Limited',
    OGDC: 'Oil & Gas Development Company Limited',
    FFC: 'Fauji Fertilizer Company Limited',
    HBL: 'Habib Bank Limited',
    HUBC: 'The Hub Power Company Limited',
    PPL: 'Pakistan Petroleum Limited',
    POL: 'Pakistan Oilfields Limited',
    EFERT: 'Engro Fertilizers Limited',
    MCB: 'MCB Bank Limited',
    UBL: 'United Bank Limited',
    DGKC: 'D.G. Khan Cement Company Limited',
    PSO: 'Pakistan State Oil Company Limited',
    SEARL: 'The Searle Company Limited',
    MLCF: 'Maple Leaf Cement Factory Limited',
    BAHL: 'Bank AL Habib Limited',
    MARI: 'Mari Petroleum Company Limited',
    TRG: 'TRG Pakistan Limited',
    ATRL: 'Attock Refinery Limited',
    UNITY: 'Unity Foods Limited',
    SYS: 'Systems Limited',
    MEBL: 'Meezan Bank Limited',
    GHNI: 'Ghandhara Industries Limited',
    PIOC: 'Pioneer Cement Limited',
    CHCC: 'Cherat Cement Company Limited',
    PAEL: 'Pak Elektron Limited',
    ISL: 'International Steels Limited',
    KAPCO: 'Kot Addu Power Company Limited'}
   
    
    if (props.type==='shortterm') {
        return(
        
            <div className="cards">
                {list_companies.map((item,index)=>{
                    const Alink= 'https://dps.psx.com.pk/company/'+item;
                    
                    return(
                        <Card 
                        comp={item}
                        type={props.type}
                        fullname={fullname[item]}
                        link = {Alink}             
                        />
                    )
                })}
            </div>    
        )

    }
    
    else{
        return(
        
            <div className="cards">
                {list_companies.map((item,index)=>{
                    const Alink= 'https://dps.psx.com.pk/company/'+item;
                    return(
                        <Card 
                        comp={item}
                        fullname={fullname[item]}
                        variable={props.variable}
                        logo = "https://www.pngitem.com/pimgs/m/267-2671122_engro-fertilizers-limited-logo-engro-polymer-and-chemicals.png"
                        link = {Alink}             
                        />
                    )
                })}
            </div>    
        )
    }
    
}

export default Home;